import { Link } from 'react-router-dom';
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { mq, pink, yellow } from './const';
import Board from './Board';
import Button from './Button';

function Naviboard({ active, comp, isActive, isComp }) {
  const flexbox = css`
    display: flex;
    justify-content: space-evenly;
    ${mq('sp')} {
      justify-content: space-around;
    }
  `;
  const sizeResp = css`
    --size: 3;
    ${mq('tab')} {
      --size: 2;
    }
    ${mq('sp')} {
      --size: 1.5;
    }
  `;

  return (
    <Board cssName={[flexbox, isActive ? pink : yellow]}>
      <Link to='/'>
        <Button
          cssName={[
            sizeResp,
            css`
              --color: #fff;
            `,
          ]}
        >
          
        </Button>
      </Link>
      {isActive || active.length < 0 || (
        <Link to='/active'>
          <Button cssName={[yellow, sizeResp]}> {active.length}</Button>
        </Link>
      )}
      {isComp || (
        <Link to='/comp'>
          <Button cssName={[pink, sizeResp]}> {comp.length}</Button>
        </Link>
      )}
    </Board>
  );
}

export default Naviboard;
